import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import City from '@/components/city'
import Detail from '@/components/detail'
Vue.use(Router)
 
export default new Router({
  routes: [
    {
      path: '/',
      component: Home
    },
    {
      path: '/city',
      name: "City",
      component: City
    },
    {
      path: '/detail/:id',
      name: "Detail",
      component: Detail
    }
  ]
})



